                                            '__cdata': ' Ta kontakt med bedrifter på messen som du gjerne vil vite mer om. Spør om bedriften har ledige praksisstillinger, lærlingeordning eller traineeprogram. Legg igjen CV-en din slik at bedriften kan kontakte deg.',
