                                            '__cdata': ' Hva vil du si? Spør familie eller venner om hjelp til å trene på intervjusituasjonen, eller tren selv foran speilet. Tenk gjennom dine egne grenser for hvor privat du vil være. Arbeidsgiver har ikke rett til å vite politisk orientering, seksuell legning, hvilken religion du tilhører eller om du er fagorganisert.',
